.hero {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.item {

	& img {
		max-height: 300px;
	}
}

.hero__infowindow {
	z-index: 500;
	width: 90%;
	text-align: center;
    background: linear-gradient(180deg,rgba(0,22,34,.36) 0,#001622);
    padding: 1rem;
	color: $white;
	position: absolute;
	bottom: 0;

	& h2 {
		font-size: 1.5em;
		margin-bottom: 1rem;
	}

	& h4 {
		font-size: .8em;
		margin-bottom: 1rem;
	}

	& a {
		text-decoration: none;
		color: $white;
		text-transform: uppercase;
		padding-bottom: 6px;
		border-bottom: 1px solid $red;

		&:hover {
			border-bottom: 4px solid $red;
		}
	}
}

.inside {

	display: flex;
	flex-direction: column;
	align-items: center;
	color: $white;

	& h2 {
		font-size: 2em;
		margin-bottom: 1rem;
	}

	& span {
		font-size: 1.5rem;
		margin-bottom: 1rem;
	}

	video {
		width: 100%;
	}
}

.teams{
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 400px;
	

	.atlanta__falcons{

		.falcons__logo {
			background-image: url(../../assets/img/falcons-logo-sm.png);
			background-repeat: no-repeat;
			width: 200px;
			height: 200px;
			z-index: 5;
		}
	}

	.atlanta__united{

		.united__logo {
			background-image: url(../../assets/img/unitedfc-logo-sm.png);
			background-repeat: no-repeat;
			width: 200px;
			height: 200px;
			z-index: 5;
		}
	}
}


















